.app {
  box-sizing: border-box;
  position: relative;
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
}

.forecast {
  box-sizing: border-box;
  border: 1px solid red;
}

.fact {
  box-sizing: border-box;
  border: 1px solid green;
  height: 370px;
  /* height: 10; */
}

@media (max-width: 600px) {
  .fact {
    height: 61vw;
    background: red;
  }
}


.forecast__item {
  box-sizing: border-box;
  border: 1px solid blue;
  height: 50px;
  cursor: pointer;
  transition: 100ms linear;
}

.forecast__item-full {
  box-sizing: border-box;
  border: 1px solid blue;
  height: 200px;
  cursor: pointer;
  transition: 100ms linear;
}